.nav-cont{
    background-color: #003173;
    padding: 10px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
}
.nav-search{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.searching{
    height: 35px;
    border-radius: 5px;
    outline: none;
    text-align: right;
    direction: rtl;
    border: #bf2051 0.25px solid;

}
.searching::-webkit-input-placeholder{
text-align: right;
direction: rtl;
}
.searching::placeholder{
text-align: right;
direction: rtl;
}

.nav-links{
    
    width: 50%;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    align-items: center;
    color: white;
    font-size: 20px;

}
.nav-links a{
    text-decoration: none;
}
.nav-links a:hover{
    color: #bf2051;
    transition: all 0.4s ease-in ;
    -moz-transition: all 0.4s ease-in;
    -webkit-transition: all 0.4s ease-in;
    -ms-transition: all 0.4s ease-in;
    -o-transition: all 0.4s ease-in;
}



.main-cont{
    /* background-color: #d6d6d6; */
    width:90%;
    margin: auto;
    /* height:90vh; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: start;
    padding: 4%;
    padding-top: 2%;

    margin-bottom: 50px;
    background-color: aliceblue;
    border-radius: 20px;
}
.main-wrapper{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.main-image {
    flex: 1 1;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.image-blue{
    width: 80%;
    height: 80%;
    display: flex;
    justify-content: center;
    background-color: #003173;
    /* border: solid 5px #003173; */
    border-radius: 20px;
    position: absolute;
    bottom: 0;
    z-index: 0;
    
}



.main-image img{
    position: relative;
    z-index: 1;
}



.main-content{
    flex: 2 1;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: start;
    text-align: right;
    direction: rtl;
}
.main-h1{
    text-align: right;
    direction: rtl;
    font-weight: bold;
    margin-bottom: 20px;
}
.main-p{
    text-align: right;
    direction: rtl;
    font-size: 25px;
    margin-bottom: 50px;

}

.main-btn{
    width:60%;
    height: 60px;
    font-size: 25px;
    font-weight: 500;
    border: none;
    background-color: #003173;
    color: #fff;
    border-radius: 10px;
}

.main-btn:hover{
border: 2px solid #003173;
scale: 1.05;
transition: all 0.3s ease-in ;
-moz-transition: all 0.3s ease-in;
-webkit-transition: all 0.3s ease-in;
-ms-transition: all 0.3s ease-in;
-o-transition: all 0.3s ease-in;
}
